/*---------------------------------------------------------------------------*
 * Start: Helper clearfix
----------------------------------------------------------------------------*/
.h-clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }

  &:after { clear: both; }
}

.mt-10{
  margin-top:10px;
}
/*---------------------------------------------------------------------------*
 * Endof: Helper clearfix
----------------------------------------------------------------------------*/
